<script>
  import { computed, onMounted, onUnmounted, nextTick } from 'vue';
  import { useDataStore } from '@/stores/dataStore';
  import * as d3 from 'd3';
  import L from 'leaflet';
  import { exportContainerSvgAsPng } from '@/utils/exportImage';

  export default {
    //  Vue DevTools 
    name: 'CaseCountView',

    setup() {
      //  Store
      //  Pinia  useDataStore 
      const dataStore = useDataStore();

      // ====================  (Computed Properties) ====================
      // 
      // 
      // 

      /**
       * 
       *
       * 
       * 12
       * 
       *
       * 
       * 1.  dataStore 12
       * 2. 
       * 3. 12 v-for 
       *
       * 
       * 
       * - id: DOMD3.js
       * - title: 
       * - agencyData: 
       * - subUnits: 
       *
       * 
       *  v-for 12
       * 
       */
      const getSupervisorChartsData = computed(() => {
        // 112
        // getTop10AgenciesWithSubUnits 
        // slice(0, 12) 1212
        const top12AgenciesWithSubUnits = dataStore.getTop10AgenciesWithSubUnits.slice(0, 12);

        // 2
        //  map 
        return top12AgenciesWithSubUnits.map((agency, index) => ({
          // IDsupervisor-chart-1, supervisor-chart-2, ...
          // IDD3.jsDOM
          id: `supervisor-chart-${index + 1}`,

          // 
          // 
          title: agency.name,

          // 
          // 
          agencyData: {
            name: agency.name, // 
            : agency., // 
            _JSON: agency._JSON, // 
          },

          // 
          //  || 
          //  subUnits 
          subUnits: agency.subUnits || [],
        }));
      });

      /**
       * 
       *
       * 
       * 
       * 
       *
       * 
       * 1. 
       * 2. 
       * 3. 
       * 4. 
       *
       * 
       * 
       */
      const debugInfo = computed(() => ({
        // 
        // 
        // true false 
        hasData: dataStore.supervisorAgencies.length > 0,

        // 
        // 10
        // 
        topUnitsCount: dataStore.getTopSupervisorAgencies.length,

        // 
        //  dataStore 
        // true false 
        loading: dataStore.loading,

        // 
        //  dataStore 
        //  null
        error: dataStore.error,
      }));

      // ====================  (Chart Rendering Functions) ====================
      //  D3.js 
      // 

      /**
       *  - 
       *
       * 
       * -  D3.js  SVG 
       * - 83
       * -  bar 
       * - 
       * - 
       *
       * @param {Object} config - 
       * @param {string} config.containerId - DOM  ID
       * @param {Array} config.data - 
       * @param {string} config.yAxisLabel - Y 
       * @param {number} [config.containerHeight] - 
       * @param {() => number} [config.getContainerHeight] - 
       * @param {() => number} [config.getChartHeight] -  bar 
       */
      const drawChart = (config) => {
        // ====================  ====================

        //  D3.js  DOM 
        // d3.select(): D3.js  document.querySelector()
        // `#${config.containerId}`:  CSS  "#main-chart"
        const container = d3.select(`#${config.containerId}`);

        //  DOM 
        // container.empty(): 
        // container.node():  DOM 
        // 
        if (container.empty() || !container.node()) {
          // eslint-disable-next-line no-console
          console.error(` #${config.containerId} `);
          return; // 
        }

        // 
        // selectAll('*'): 
        // remove(): D3.js  DOM 
        container.selectAll('*').remove();

        // ====================  ====================

        // 
        // config.data:  name  value 
        const chartData = config.data;

        //  Y Y
        // config.yAxisLabel:  Y 

        // 
        // !chartData:  null  undefined
        // chartData.length === 0: 
        // 
        if (!chartData || chartData.length === 0) {
          // 
          container
            .append('div') //  div 
            .attr('class', 'd-flex align-items-center justify-content-center h-100') // Bootstrap 100%
            .style('color', '#666') // 
            .text('...'); // 
          return; // 
        }

        // ====================  ====================

        // 
        // getBoundingClientRect():  API
        // .width: 
        const containerWidth = container.node().getBoundingClientRect().width;

        // 320
        const containerHeight =
          (typeof config.getContainerHeight === 'function'
            ? config.getContainerHeight()
            : config.containerHeight) || 320;

        // margin
        // 
        // ||: 
        const margin = config.margin || {
          top: 20, // 
          right: 0, // 0
          bottom: 160, // X160px
          left: 40, // 32pxY
        };

        // bar160px
        const barAreaHeight =
          (typeof config.getChartHeight === 'function' ? config.getChartHeight() : null) || 160;

        // 
        // 
        const width = containerWidth - margin.left - margin.right;
        // bar160px
        const height = barAreaHeight - margin.top;

        // ==================== SVG  ====================

        //  SVG 
        const svg = container
          .append('svg') //  SVG 
          .attr('width', containerWidth) //  SVG 
          .attr('height', containerHeight); //  SVG 

        // group
        // 
        // append('g'):  SVG 
        // attr('transform', ...): 
        // translate(${margin.left},${margin.top}): 
        const g = svg.append('g').attr('transform', `translate(${margin.left},${margin.top})`);

        // ==================== ====================

        // 
        // config.containerId === 'main-chart':  ID 
        // 83
        const isMainChart = config.containerId === 'main-chart';

        // 
        // :  ?  : 
        // 126
        const maxBars = isMainChart ? 12 : 6;

        //  N 
        // slice(0, maxBars): JavaScript 0 maxBars 
        // 
        const topData = chartData.slice(0, maxBars);

        // 
        // 
        // 83
        // Array.from({ length: maxBars }, callback): 
        const displayData = Array.from({ length: maxBars }, (_, i) => {
          // 
          if (topData[i]) {
            // 
            return {
              ...topData[i], // name, value, fullName
              position: i, //  0, 1, 2, ...
              uniqueName: `pos-${i}-${topData[i].name}`, // : pos-0-, pos-1-, ...
            };
          } else {
            // 
            return {
              name: `${i + 1}`, // 
              value: 0, // 0
              fullName: '', // 
              isEmpty: true, // 
              position: i, // 
              uniqueName: `pos-${i}-empty`, // : pos-3-empty, pos-4-empty, ...
            };
          }
        });

        // X  justify-content: space-around 
        const barWidth = 16; // 16px

        //  space-around 
        const dataCount = displayData.length;
        const totalBarWidth = dataCount * barWidth; // bar
        const availableSpaceWidth = width - totalBarWidth; // 
        const spaceUnit = availableSpaceWidth / (dataCount * 2); // space-around

        // 
        const xScale = (uniqueName) => {
          const index = displayData.findIndex((d) => d.uniqueName === uniqueName);
          if (index === -1) return 0;
          // space-around:  spaceUnitbarspace
          return spaceUnit + index * (barWidth + 2 * spaceUnit) + barWidth / 2;
        };

        // Y  SVG 
        const maxValue = d3.max(topData, (d) => d.value) || 1; // 

        // 5
        const getRoundedMax = (val) => {
          // 5
          let interval = 5;

          // 
          if (val > 1000) {
            // 10005
            interval = Math.ceil(val / 5000) * 250; // 250, 500, 750, 1000, 1250...
            if (interval < 500) interval = 500;
          } else if (val > 100) {
            interval = Math.ceil(val / 500) * 25; // 25, 50, 75, 100, 125...
            if (interval < 50) interval = 50;
          } else if (val > 50) {
            interval = 10; // 50-100 10
          } else if (val > 25) {
            interval = 5; // 25-50 5
          }

          // 5
          if (interval % 5 !== 0) {
            interval = Math.ceil(interval / 5) * 5;
          }

          // 
          const roundedMax = Math.ceil(val / interval) * interval;

          // 2-5
          if (roundedMax / interval < 2) {
            return interval * 2; // 2
          }

          return roundedMax;
        };

        const roundedMaxValue = getRoundedMax(maxValue);
        const yScale = d3
          .scaleLinear() // 
          .domain([0, roundedMaxValue]) // 0
          .range([height, 0]); // SVGY

        // Y550
        const calculateYTicks = (roundedMax) => {
          const ticks = [0]; // 0

          // 5
          let interval = 5;

          // 5
          while (roundedMax / interval > 4) {
            interval += 5; // 5
          }

          // intervalinterval
          for (let i = interval; i <= roundedMax; i += interval) {
            ticks.push(i);
            if (ticks.length >= 5) break; // 5
          }

          return ticks;
        };

        const yTicks = calculateYTicks(roundedMaxValue);

        // ====================  ====================
        //  tooltip 
        //  tooltip 

        // ==================== ====================

        //  D3.js 
        //  D3.js Data-Driven Documents
        g.selectAll('.bar') //  'bar' 
          .data(displayData) //  displayData 
          .enter() // 
          .append('rect') //  SVG 
          .attr('class', 'bar') //  'bar' CSS 
          // 
          .attr('x', (d) => xScale(d.uniqueName) - barWidth / 2) // X 
          .attr('y', (d) => yScale(d.value)) // Y  yScale SVG 
          .attr('width', barWidth) // 32px
          .attr('height', (d) => height - yScale(d.value)) //  Y 
          .attr('fill', 'var(--my-color-blue)'); //  CSS 

        // ====================  ====================
        // 
        // 0
        // filter(): JavaScript 
        // !d.isEmpty: 
        // d.value > 0: 0
        const labelData = displayData.filter((d) => !d.isEmpty && d.value > 0);

        //  D3.js 
        g.selectAll('.bar-label') //  'bar-label' 
          .data(labelData) // 
          .enter() // 
          .append('text') //  SVG 
          .attr('class', 'bar-label') //  CSS 
          .attr('x', (d) => {
            // X 
            // xScale(d.uniqueName):  X 
            //  scalePoint 
            return xScale(d.uniqueName);
          })
          .attr('y', (d) => {
            // Y 
            // yScale(d.value):  Y 
            // - 5: 5
            // SVG  Y 
            return yScale(d.value) - 5;
          })
          .attr('text-anchor', 'middle') // SVG  X 
          .style('font-size', '12px') // CSS 12
          .style('fill', '#333') // CSS  (#333)
          .style('font-weight', 'bold') // CSS 
          .text((d) => d3.format(',')(d.value)); //  d3.format(',') 

        // ====================  ====================

        // Y0
        g.selectAll('.grid-line')
          .data(yTicks) // Y0
          .enter()
          .append('line') // 
          .attr('class', 'grid-line') // CSS
          .attr('x1', 0) // X
          .attr('x2', width) // X
          .attr('y1', (d) => yScale(d)) // Y
          .attr('y2', (d) => yScale(d)) // Y
          .attr('stroke', '#bdbdbd') //  gray-400  (#bdbdbd)
          .attr('stroke-width', 1) // 
          .attr('stroke-dasharray', '3,3') // 33
          .attr('opacity', (d) => (d === 0 ? 0.8 : 0.4)); // 0

        // X 
        const xAxisGroup = g
          .append('g') //  X 
          .attr('transform', `translate(0,${height})`); //  X 

        //  xScale  d3.axisBottom
        //  tickSize(0)

        //  X 
        xAxisGroup.selectAll('text').remove();

        //  X 
        displayData.forEach((dataItem) => {
          if (!dataItem.isEmpty && dataItem.name) {
            //  dataItem.name 
            const x = xScale(dataItem.uniqueName); //  bar  x 
            const text = dataItem.name;

            // 
            const textGroup = xAxisGroup.append('g').attr('transform', `translate(${x}, 20)`); //  bar20px

            // 10
            const lines = [];
            for (let i = 0; i < text.length; i += 10) {
              lines.push(text.substring(i, i + 10));
            }

            // 
            const totalLines = lines.length;

            lines.forEach((line, lineIndex) => {
              // 
              const lineOffset =
                totalLines === 1
                  ? 0
                  : totalLines === 2
                    ? lineIndex === 0
                      ? -8
                      : 8
                    : totalLines === 3
                      ? lineIndex === 0
                        ? -12
                        : lineIndex === 1
                          ? 0
                          : 12
                      : // 4
                        (lineIndex - (totalLines - 1) / 2) * 8;

              // 
              line.split('').forEach((char, charIndex) => {
                textGroup
                  .append('text')
                  .text(char)
                  .attr('x', lineOffset) // 
                  .attr('y', charIndex * (12 + 0.6)) // 12px + letter-spacing 0.6px
                  .style('font-size', '12px')
                  .style('font-family', 'Arial, sans-serif')
                  .style('fill', '#333')
                  .style('text-anchor', 'middle'); // 
              });
            });
          }
        });

        // Y 
        g.append('g') //  Y 
          .call(
            d3
              .axisLeft(yScale)
              .tickValues(yTicks) // 
              .tickSize(0) // 
              .tickFormat((d) => {
                // 
                return d3.format(',')(d); // kM
              }) // 
          )
          .style('font-size', '11px') // CSS  Y 
          .select('.domain')
          .remove(); // Y

        // Y
        g.selectAll('.tick text')
          .style('fill', '#666') // 
          .style('font-weight', 'normal'); // 

        // YY
      };

      /**
       * 
       * 
       *
       * 
       * 1. 8
       * 2. 3
       * 3. 
       *
       * 
       * -  drawChart 
       * - 
       * - 
       */
      const drawSupervisorCharts = () => {
        // 12
        // .value: Vue 3 Composition API 
        const chartsData = getSupervisorChartsData.value;

        // 
        if (!chartsData || chartsData.length === 0) {
          return;
        }

        // 
        // forEach(): JavaScript 
        chartsData.forEach((chartData) => {
          // 
          if (!chartData || !chartData.subUnits) {
            return;
          }

          // 
          // 6
          // slice(0, 6): 6
          // map(): 
          const subUnitsData = chartData.subUnits
            .slice(0, 6)
            .map((unit) => {
              // 
              if (!unit || !unit.name_sub) {
                return null;
              }

              return {
                name: unit.name_sub, // namedrawChart
                uniqueName: unit.name_sub,
                value: unit. || 0, // 
                extra: {
                  count: unit. || 0, // 
                  budget: Math.round(unit._ || 0), // _
                },
              };
            })
            .filter(Boolean); //  null 

          // 
          if (subUnitsData.length === 0) {
            return;
          }

          // 
          const chartConfig = {
            // IDHTMLIDD3.js
            containerId: chartData.id,
            // 
            data: subUnitsData,
            // Y
            yAxisLabel: '',
            // 160px bar + 160px
            containerHeight: 320,
            // TooltipHTML
            tooltipTemplate: (d) => `
              <strong>${d.fullName}</strong><br/>
              : ${d.value}<br/>
              : ${d.extra.budget}<br/>
              : ${d.extra.agencyName}
            `,
          };

          // 
          // 
          drawChart(chartConfig);
        });
      };

      // ====================  ====================

      /**
       * 
       * 
       *
       * 
       * 1.  dataStore 
       * 2. 8
       * 3. 
       * 4.  X 
       *
       * @returns {Array} 
       */
      const prepareMainChartData = () => {
        const topAgencies = dataStore.getTopSupervisorAgencies;

        // 
        if (!topAgencies || topAgencies.length === 0) {
          return [];
        }

        return topAgencies
          .map((agency) => {
            //  agency 
            if (!agency || !agency.name) {
              return null;
            }

            return {
              name: agency.name, // name
              uniqueName: agency.name,
              value: agency. || 0, // 
              extra: {
                count: agency. || 0, // 
                budget: agency._ || 0, // _
              },
            };
          })
          .filter(Boolean); //  null 
      };

      // ====================  ====================

      /**
       * 
       * 
       *
       * 
       *  "":"TRUE" 
       *
       * 
       * 1. "":"TRUE"
       * 2. 
       * 3. 
       * 4. 
       * 5. 
       *
       * 
       * - type: 'agency'
       * - type: 'unit'
       *
       * 
       * 
       * 
       *
       * 
       * {
       *   nodes: Array,  // 
       *   links: Array   // 
       * }
       *
       * 
       *  D3.js 
       * 
       */
      const prepareNetworkGraphData = () => {
        // 1. 
        const supervisorAgencies = dataStore.supervisorAgencies;
        if (!supervisorAgencies || supervisorAgencies.length === 0) {
          console.log('');
          return { nodes: [], links: [] };
        }

        // 2. 
        const academicAgencies = new Set();
        supervisorAgencies.forEach((agency) => {
          if (agency. === 'TRUE') {
            academicAgencies.add(agency.name);
          }
        });

        console.log(':', [...academicAgencies]);

        // 3. 
        const mappingData = dataStore.supervisorExecutingMapping;
        if (!mappingData || mappingData.length === 0) {
          console.log('');
          return { nodes: [], links: [] };
        }

        // 4. 
        const academicMappings = mappingData.filter((item) => academicAgencies.has(item.name));

        console.log(':', mappingData.length);
        console.log(':', academicMappings.length);

        if (academicMappings.length === 0) {
          console.log('');
          return { nodes: [], links: [] };
        }

        // 5. 
        const academicUnits = new Set();
        academicMappings.forEach((item) => {
          // 
          const unitData = dataStore.executingUnits.find((unit) => unit.name === item.name_sub);
          if (unitData && unitData. === 'TRUE') {
            academicUnits.add(item.name_sub);
          }
        });

        // 6. 
        let nodes = [];

        // 
        academicAgencies.forEach((agencyName) => {
          // 
          const agencyMappings = academicMappings.filter((item) => item.name === agencyName);
          const totalCount = agencyMappings.reduce((sum, item) => sum + (item. || 0), 0);
          const totalBudget = agencyMappings.reduce(
            (sum, item) => sum + (item._ || 0),
            0
          );
          const projectCount = agencyMappings.length;

          nodes.push({
            id: `agency-${agencyName}`,
            name: agencyName,
            type: 'agency',
            totalCount: totalCount,
            totalBudget: totalBudget,
            projectCount: projectCount,
            meanBudget: projectCount > 0 ? totalBudget / projectCount : 0,
            isAcademic: true, // 
            academicStatus: 'TRUE', // 
          });
        });

        // 
        academicUnits.forEach((unitName) => {
          // 
          const unitMappings = academicMappings.filter((item) => item.name_sub === unitName);
          const totalCount = unitMappings.reduce((sum, item) => sum + (item. || 0), 0);
          const totalBudget = unitMappings.reduce(
            (sum, item) => sum + (item._ || 0),
            0
          );
          const projectCount = unitMappings.length;

          // 
          const unitData = dataStore.executingUnits.find((item) => item.name === unitName);
          const unitAcademicStatus = unitData ? unitData. : 'FALSE';

          nodes.push({
            id: `unit-${unitName}`,
            name: unitName,
            type: 'unit',
            totalCount: totalCount,
            totalBudget: totalBudget,
            projectCount: projectCount,
            meanBudget: projectCount > 0 ? totalBudget / projectCount : 0,
            isAcademic: unitAcademicStatus === 'TRUE',
            academicStatus: unitAcademicStatus, //  JSON 
          });
        });

        // 7. 
        let links = academicMappings
          .filter((item) => academicUnits.has(item.name_sub)) // 
          .map((item) => ({
            source: `agency-${item.name}`,
            target: `unit-${item.name_sub}`,
            value: item. || 0, // 
          }));

        // 8.  1 
        //    
        //    -  type==='unit'  totalCount === 1 
        //    -  nodes/links  'unit-'
        //    -  agency  'unit-' 
        const unitNodesToMerge = new Set(
          nodes.filter((n) => n.type === 'unit' && Number(n.totalCount) === 1).map((n) => n.id)
        );

        if (unitNodesToMerge.size > 0) {
          // 
          let aggTotalCount = 0;
          let aggTotalBudget = 0;
          let aggProjectCount = 0;

          nodes
            .filter((n) => unitNodesToMerge.has(n.id))
            .forEach((n) => {
              aggTotalCount += Number(n.totalCount) || 0;
              aggTotalBudget += Number(n.totalBudget) || 0;
              aggProjectCount += Number(n.projectCount) || 0;
            });

          //  agency 
          const linkAggBySource = new Map(); // key: sourceId, val: sumValue
          links
            .filter((l) => unitNodesToMerge.has(l.target))
            .forEach((l) => {
              const prev = linkAggBySource.get(l.source) || 0;
              linkAggBySource.set(l.source, prev + (Number(l.value) || 0));
            });

          // 
          const keptLinks = links.filter((l) => !unitNodesToMerge.has(l.target));

          // 
          if (aggProjectCount > 0 || aggTotalCount > 0) {
            const otherNodeId = 'unit-';
            const otherNode = {
              id: otherNodeId,
              name: '',
              type: 'unit',
              totalCount: aggTotalCount,
              totalBudget: aggTotalBudget,
              projectCount: aggProjectCount,
              meanBudget: aggProjectCount > 0 ? aggTotalBudget / aggProjectCount : 0,
              isAcademic: true,
              academicStatus: 'TRUE',
            };

            //  agency 
            const aggregatedLinks = Array.from(linkAggBySource.entries()).map(
              ([sourceId, sumValue]) => ({
                source: sourceId,
                target: otherNodeId,
                value: sumValue || 1,
              })
            );

            //  nodes  links return
            const keptNodes = nodes.filter((n) => !unitNodesToMerge.has(n.id));
            keptNodes.push(otherNode);
            nodes = keptNodes;
            links = keptLinks.concat(aggregatedLinks);
          }
        }

        // 9.  > 10
        const MIN_COUNT = 10;
        const allowedNodeIds = new Set(
          nodes.filter((n) => Number(n.totalCount) > MIN_COUNT).map((n) => n.id)
        );
        nodes = nodes.filter((n) => allowedNodeIds.has(n.id));
        links = links.filter((l) => allowedNodeIds.has(l.source) && allowedNodeIds.has(l.target));

        return { nodes, links };
      };

      // 

      /**
       * 
       *
       * 
       *  D3.js 
       * 
       * 
       *
       * 
       * - D3.js 
       * - SVG 
       * -  prepareNetworkGraphData 
       * - 
       *
       * 
       * - 
       * - 
       * - 
       * - 
       *
       * 
       * - 
       * - 
       * - 
       * - 
       *
       * 
       * - 
       * -  D3.js 
       * - 
       */
      const drawNetworkGraph = () => {
        // 
        const graphData = prepareNetworkGraphData();

        // 
        if (!graphData.nodes || graphData.nodes.length === 0) {
          const container = d3.select('#network-graph');
          container.html('<div class="text-center text-muted p-5"></div>');
          return;
        }

        // 
        const container = d3.select('#network-graph');
        container.selectAll('*').remove();

        // 
        const containerRect = container.node().getBoundingClientRect();
        const width = containerRect.width;
        const height = 600; // CSS

        //  SVG 
        const svg = container.append('svg').attr('width', width).attr('height', height);

        // 
        const g = svg.append('g');

        // 
        const zoom = d3
          .zoom()
          .scaleExtent([0.1, 3]) // 0.1x  3x
          .on('zoom', (event) => {
            // 
            g.attr('transform', event.transform);
          });

        //  SVG 
        svg.call(zoom);

        // 
        const simulation = d3
          .forceSimulation(graphData.nodes)
          .force(
            'link',
            d3
              .forceLink(graphData.links)
              .id((d) => d.id) //  ID 
              .distance(100)
          ) // 
          .force('charge', d3.forceManyBody().strength(-300)) // 
          .force('center', d3.forceCenter(width / 2, height / 2)) // 
          .force(
            'collision',
            d3.forceCollide().radius((d) => {
              // 
              // 15005050 = 2500
              // 1 = 1
              //  =  = (/)
              const scaleMultiplier = 1.0; // 1.0 = 2.0 = 20.5 = 2
              const area = d.totalCount * scaleMultiplier; //  =   
              const radius = Math.sqrt(area / Math.PI);
              return Math.max(5, Math.min(radius, 100)); // 5px10px100px200px
            })
          ); // 

        // 
        const links = g
          .append('g')
          .attr('class', 'links')
          .selectAll('line')
          .data(graphData.links)
          .enter()
          .append('line')
          .attr('stroke', '#999') // 
          .attr('stroke-opacity', 0.6) // 
          .attr('stroke-width', 2); // 

        // 
        const nodes = g
          .append('g')
          .attr('class', 'nodes')
          .selectAll('circle')
          .data(graphData.nodes)
          .enter()
          .append('circle')
          .attr('r', (d) => {
            // 
            // 15005050 = 2500
            // 1 = 1
            //  =  = (/)
            const scaleMultiplier = 5.0; // 1.0 = 2.0 = 20.5 = 2
            const area = d.totalCount * scaleMultiplier; //  =   
            const radius = Math.sqrt(area / Math.PI);
            return Math.max(5, Math.min(radius, 100)); // 5px10px100px200px
          })
          .attr('fill', (d) => (d.type === 'agency' ? '#4a90e2' : '#f5a623')) // 
          .attr('stroke', '#fff') // 
          .attr('stroke-width', 2) // 
          .style('cursor', 'pointer') // 
          .call(
            d3
              .drag() // 
              .on('start', dragstarted) // 
              .on('drag', dragged) // 
              .on('end', dragended)
          ); // 

        // 
        const labels = g
          .append('g')
          .attr('class', 'labels')
          .selectAll('text')
          .data(graphData.nodes)
          .enter()
          .append('text')
          .text((d) => {
            // 10
            return d.name.length > 10 ? d.name.substring(0, 10) + '...' : d.name;
          })
          .attr('font-size', '12px')
          .attr('font-family', 'Arial, sans-serif')
          .attr('fill', '#333')
          .attr('text-anchor', 'middle') // 
          .attr('dy', '0.35em') // 
          .style('pointer-events', 'none'); // 

        //  tooltip 
        const tooltip = d3
          .select('body')
          .append('div')
          .attr('class', 'network-tooltip')
          .style('position', 'absolute')
          .style('padding', '10px')
          .style('background', 'rgba(0, 0, 0, 0.8)')
          .style('color', 'white')
          .style('border-radius', '5px')
          .style('font-size', '12px')
          .style('line-height', '1.4')
          .style('pointer-events', 'none')
          .style('opacity', 0)
          .style('z-index', '1000');

        // 
        nodes
          .on('mouseover', function (event, d) {
            // 
            d3.select(this)
              .transition()
              .duration(200)
              .attr('stroke-width', 3)
              .attr('stroke', '#333');

            // 
            const typeText = d.type === 'agency' ? '' : '';

            let tooltipContent = `
              <div style="font-weight: bold; margin-bottom: 5px;">${typeText}</div>
              <div style="font-size: 14px; font-weight: bold; margin-bottom: 8px;">${d.name}</div>
              <div>: <span style="color: #4a90e2;">${d.totalCount}</span></div>
              <div>: <span style="color: #50e3c2;">${Math.round(d.totalBudget / d.projectCount || 0).toLocaleString()}</span>()</div>
            `;

            // 
            if (d.type === 'unit') {
              tooltipContent += `<div style="margin-top: 5px; color: #f5a623;"> </div>`;
            }

            //  tooltip
            tooltip
              .html(tooltipContent)
              .style('opacity', 1)
              .style('left', event.pageX + 10 + 'px')
              .style('top', event.pageY - 10 + 'px');
          })
          .on('mousemove', function (event) {
            // 
            tooltip.style('left', event.pageX + 10 + 'px').style('top', event.pageY - 10 + 'px');
          })
          .on('mouseout', function () {
            // 
            d3.select(this)
              .transition()
              .duration(200)
              .attr('stroke-width', 2)
              .attr('stroke', '#fff');

            //  tooltip
            tooltip.transition().duration(200).style('opacity', 0);
          });

        // 
        simulation.on('tick', () => {
          // 
          links
            .attr('x1', (d) => d.source.x)
            .attr('y1', (d) => d.source.y)
            .attr('x2', (d) => d.target.x)
            .attr('y2', (d) => d.target.y);

          // 
          nodes.attr('cx', (d) => d.x).attr('cy', (d) => d.y);

          // 
          labels.attr('x', (d) => d.x).attr('y', (d) => d.y);
        });

        // 
        function dragstarted(event, d) {
          if (!event.active) simulation.alphaTarget(0.3).restart();
          d.fx = d.x; //  X 
          d.fy = d.y; //  Y 
        }

        // 
        function dragged(event, d) {
          d.fx = event.x; //  X 
          d.fy = event.y; //  Y 
        }

        // 
        function dragended(event, d) {
          if (!event.active) simulation.alphaTarget(0); // 
          d.fx = null; //  X 
          d.fy = null; //  Y 
        }
      };

      // ====================  ====================

      /**
       * 
       *  Leaflet.js 
       *
       * 
       * - 
       * - 
       * - 
       * - 
       * - 
       *
       * 
       * - 
       * - 
       */
      /**
       * 
       *
       * 
       * 
       * 
       * 
       *
       * 
       * -  Leaflet.js 
       * -  OpenStreetMap 
       * - 
       * - 
       *
       * 
       * 1.  dataStore "":"TRUE"
       * 2. 
       * 3. 
       * 4. 
       *
       * 
       * - 
       * - 
       * - 
       * - 
       *
       * 
       * 
       */
      const initMap = () => {
        //  Leaflet 
        const mapContainer = document.getElementById('taiwan-map');

        // 
        if (!mapContainer) {
          console.warn(' taiwan-map ');
          return;
        }

        // Leaflet _leaflet_id  Leaflet 
        // 
        if (mapContainer._leaflet_id) {
          //  Leaflet  ID 
          mapContainer._leaflet_id = null; //  Leaflet 
          mapContainer.innerHTML = ''; //  HTML 
        }

        // 
        const map = L.map('taiwan-map', {
          zoomControl: false, // 
          attributionControl: false, // bar
        }).setView([23.8, 120.9], 7);

        //  OpenStreetMap 
        L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
          attribution: '', // 
        }).addTo(map);

        // """"
        const universityUnits = dataStore.getUniversityExecutingUnitsWithLocation;

        // 
        console.log(':', {
          : universityUnits.length,
          : universityUnits.filter((u) => u.hasLocation).length,
          : universityUnits.slice(0, 3),
        });

        // 
        if (universityUnits.length === 0) {
          console.warn('');
          return;
        }

        // 
        universityUnits.forEach((unit) => {
          // 
          if (!unit.hasLocation) return;

          // 
          // 
          const scaleFactor = 10; // 
          const radius = Math.sqrt((unit. * scaleFactor) / Math.PI); // 

          // 
          const circle = L.circle([unit.lat, unit.lng], {
            color: 'var(--my-color-red)', // 
            fillColor: 'var(--my-color-red)', // 
            fillOpacity: 0.6, // 
            radius: radius * 1000, // 
            weight: 1, // 
          }).addTo(map);

          // 
          const popupContent = `
            <div>
              <strong>${unit.name}</strong><br/>
              <div>
                <div>: ${unit..toLocaleString()}</div>
                <div>: ${Math.round(unit._).toLocaleString()}()</div>
              </div>
            </div>
          `;

          // 
          circle.bindPopup(popupContent, {
            maxWidth: 300,
          });

          // 
          circle.on('mouseover', function () {
            this.setStyle({
              fillOpacity: 0.8,
              weight: 3,
            });
          });

          circle.on('mouseout', function () {
            this.setStyle({
              fillOpacity: 0.6,
              weight: 2,
            });
          });
        });
      };

      /**
       * 
       *
       * 
       *  D3.js 
       * 
       *
       * 
       * -  D3.js  d3-cloud 
       * - 
       * - 
       * - 
       *
       * 
       * 1.  dataStore "":"TRUE"
       * 2. 
       * 3. 
       * 4.  d3-cloud 
       *
       * 
       * - 
       * - 
       * - 
       * - 
       *
       * 
       * - 
       * -  D3.js 
       * - 
       */
      const drawWordCloud = () => {
        // 
        const container = d3.select('#word-cloud');
        if (container.empty()) {
          console.warn(' #word-cloud ');
          return;
        }

        // 
        container.selectAll('*').remove();

        // 
        const academicUnits = dataStore.executingUnits.filter(
          (unit) => unit. === 'TRUE' && unit. > 0
        );

        if (academicUnits.length === 0) {
          container
            .append('div')
            .attr('class', 'd-flex align-items-center justify-content-center h-100')
            .style('color', '#666')
            .text('');
          return;
        }

        // 
        const countsForScale = academicUnits.map((u) => u. || 0);
        const minCountForScale = d3.min(countsForScale) || 0;
        const maxCountForScale = d3.max(countsForScale) || 1;
        const sizeScale = d3
          .scalePow()
          .exponent(1.15)
          .domain([minCountForScale, maxCountForScale])
          .range([12, 44]);

        // 
        const wordData = academicUnits.map((unit) => ({
          text: unit.name,
          size: Math.max(10, Math.round(sizeScale(unit. || 0))),
          count: unit.,
          budget: unit._ || 0,
        }));

        //  SVG 
        const containerWidth = container.node().getBoundingClientRect().width;
        const containerHeight = 400;

        const svg = container
          .append('svg')
          .attr('width', containerWidth)
          .attr('height', containerHeight);

        // CSS 
        const cssColors = [
          'var(--my-color-red)',
          'var(--my-color-pink)',
          'var(--my-color-deeporange)',
          'var(--my-color-orange)',
          'var(--my-color-amber)',
          'var(--my-color-yellow)',
          'var(--my-color-lime)',
          'var(--my-color-light-green)',
          'var(--my-color-green)',
          'var(--my-color-teal)',
          'var(--my-color-cyan)',
          'var(--my-color-lightblue)',
          'var(--my-color-blue)',
          'var(--my-color-bluegrey)',
          'var(--my-color-indigo)',
          'var(--my-color-deeppurple)',
          'var(--my-color-purple)',
          'var(--my-color-brown)',
        ];

        // 
        const words = [];
        const placedRects = [];

        // 
        const measureText = (text, fontSize) => {
          const tempSvg = d3
            .select('body')
            .append('svg')
            .attr('width', 0)
            .attr('height', 0)
            .style('position', 'absolute')
            .style('left', '-9999px')
            .style('top', '-9999px');
          const tempText = tempSvg
            .append('text')
            .style('font-size', `${fontSize}px`)
            .style('font-family', 'Arial, Microsoft JhengHei, sans-serif')
            .text(text);
          const bbox = tempText.node().getBBox();
          tempSvg.remove();
          return { width: bbox.width, height: bbox.height };
        };

        const centerX = containerWidth / 2;
        const centerY = containerHeight / 2;
        const marginPad = 2;

        const rectsIntersect = (a, b) =>
          !(a.right < b.left || a.left > b.right || a.bottom < b.top || a.top > b.bottom);

        const sortedWordData = [...wordData].sort((a, b) => b.size - a.size);

        sortedWordData.forEach((word) => {
          let placed = false;

          // 
          for (let trySize = word.size; trySize >= 10 && !placed; trySize -= 2) {
            const { width, height } = measureText(word.text, trySize);

            // 
            for (let t = 0; t < 2000 && !placed; t++) {
              const angle = 0.35 * t;
              const radius = 2 + 2 * angle;
              const x = centerX + radius * Math.cos(angle);
              const y = centerY + radius * Math.sin(angle);

              const rect = {
                left: x - width / 2 - marginPad,
                right: x + width / 2 + marginPad,
                top: y - height / 2 - marginPad,
                bottom: y + height / 2 + marginPad,
              };

              // 
              if (
                rect.left < 0 ||
                rect.right > containerWidth ||
                rect.top < 0 ||
                rect.bottom > containerHeight
              ) {
                continue;
              }

              // 
              const hasCollision = placedRects.some((r) => rectsIntersect(rect, r));
              if (!hasCollision) {
                // 
                let curX = x;
                let curY = y;
                let curRect = { ...rect };
                for (let s = 0; s < 80; s++) {
                  const vx = centerX - curX;
                  const vy = centerY - curY;
                  const vlen = Math.hypot(vx, vy) || 1;
                  const step = 1; // 1px
                  const nx = curX + (vx / vlen) * step;
                  const ny = curY + (vy / vlen) * step;
                  const nrect = {
                    left: nx - width / 2 - marginPad,
                    right: nx + width / 2 + marginPad,
                    top: ny - height / 2 - marginPad,
                    bottom: ny + height / 2 + marginPad,
                  };
                  const outOfBounds =
                    nrect.left < 0 ||
                    nrect.right > containerWidth ||
                    nrect.top < 0 ||
                    nrect.bottom > containerHeight;
                  const collide = placedRects.some((r) => rectsIntersect(nrect, r));
                  if (outOfBounds || collide) break;
                  curX = nx;
                  curY = ny;
                  curRect = nrect;
                }

                const placedWord = {
                  text: word.text,
                  size: trySize,
                  budget: word.budget,
                  count: word.count,
                  x: curX,
                  y: curY,
                  color: cssColors[Math.floor(Math.random() * cssColors.length)],
                };
                words.push(placedWord);
                placedRects.push(curRect);
                placed = true;
              }
            }
          }
        });

        // 
        svg
          .selectAll('text')
          .data(words)
          .enter()
          .append('text')
          .style('font-size', (d) => `${d.size}px`)
          .style('font-family', 'Arial, Microsoft JhengHei, sans-serif')
          .style('font-weight', 'bold')
          .style('fill', (d) => d.color)
          .style('cursor', 'pointer')
          .attr('text-anchor', 'middle')
          .attr('dominant-baseline', 'middle')
          .attr('x', (d) => d.x)
          .attr('y', (d) => d.y)
          // 
          .text((d) => d.text)
          .on('mouseover', function (event, d) {
            // 
            d3.select(this).style('fill', 'var(--my-color-red)');

            //  tooltip
            const tooltip = d3
              .select('body')
              .append('div')
              .attr('class', 'word-cloud-tooltip')
              .style('position', 'absolute')
              .style('background', 'rgba(0, 0, 0, 0.8)')
              .style('color', 'white')
              .style('padding', '8px 12px')
              .style('border-radius', '4px')
              .style('font-size', '12px')
              .style('pointer-events', 'none')
              .style('z-index', '1000');

            tooltip.html(`
              <div><strong>${d.text}</strong></div>
              <div>: ${d.count.toLocaleString()}</div>
              <div>: ${Math.round(d.budget).toLocaleString()}()</div>
            `);
          })
          .on('mousemove', function (event) {
            //  tooltip
            d3.select('.word-cloud-tooltip')
              .style('left', event.pageX + 10 + 'px')
              .style('top', event.pageY - 10 + 'px');
          })
          .on('mouseout', function (event, d) {
            // 
            d3.select(this).style('font-size', `${d.size}px`).style('fill', d.color);

            //  tooltip
            d3.select('.word-cloud-tooltip').remove();
          });
      };

      // ====================  (Lifecycle Hooks) ====================
      //  Vue 
      // 

      /**
       * 
       *
       * 
       *  DOM 
       * 
       * 
       *
       * 
       * 1.  dataStore 
       *    - 
       *    - 
       *    - 
       *    - 
       *
       * 2. DOM  nextTick  Vue 
       *    - 
       *    -  DOM 
       *    - 
       *
       * 3. 
       *    - 
       *    - 12
       *    - 
       *    - 
       *
       * 4. 
       *    - 
       *    - 
       *    - 
       *
       * 
       * -  async/await 
       * - 
       * - 
       * -  Pinia store 
       */
      onMounted(async () => {
        // 
        // eslint-disable-next-line no-console
        console.log('');

        //  dataStore 
        // await: 
        // loadAllData(): dataStore 
        await dataStore.loadAllData();

        // 
        // eslint-disable-next-line no-console
        console.log(':', {
          // 
          : dataStore.supervisorAgencies.length,
          // 
          : dataStore.supervisorExecutingMapping.length,
          // N
          : dataStore.getTopSupervisorAgencies.length,
          // 
          : dataStore.loading,
          // 
          : dataStore.error,
        });

        // DOM  Vue  DOM 
        // nextTick(): Vue 3  DOM 
        // DOM 
        const redrawAll = () => {
          // 
          const mainChartConfig = {
            // DOM  ID
            containerId: 'main-chart',
            // 8
            data: prepareMainChartData(),
            // Y 
            yAxisLabel: '',
            // bar  320 480 160/320
            getChartHeight: () => 320,
            getContainerHeight: () => 480,
          };
          // 
          drawChart(mainChartConfig);

          // 
          drawSupervisorCharts();

          // 
          initMap();

          // 
          drawNetworkGraph();

          // 
          drawWordCloud();
        };

        nextTick(() => {
          redrawAll();
          let resizeTimer = null;
          const onResize = () => {
            if (resizeTimer) clearTimeout(resizeTimer);
            resizeTimer = setTimeout(() => {
              // 
              d3.select('#main-chart').selectAll('*').remove();
              redrawAll();
            }, 200);
          };
          window.addEventListener('resize', onResize);
          CaseCount_onResize = onResize;
        });
      });

      /**
       * 
       *
       * 
       *  DOM 
       *  DOM 
       * 
       *
       * 
       * 1.  D3.js 
       * 2. DOM  DOM 
       * 3. 
       * 4. 
       *
       * 
       * - 
       * - 
       * -  try-catch 
       * - 
       *
       * 
       *  selectAll('*').remove() DOM 
       * 
       * 
       */
      let CaseCount_onResize = null;
      onUnmounted(() => {
        //  D3.js 
        // selectAll('.bar-label'):  'bar-label' 
        // remove():  DOM 
        //  DOM 
        d3.selectAll('.bar-label').remove();

        //  tooltip 
        d3.selectAll('.network-tooltip').remove();

        //  tooltip 
        d3.selectAll('.word-cloud-tooltip').remove();
        if (CaseCount_onResize) window.removeEventListener('resize', CaseCount_onResize);
      });

      // ====================  (Return Object) ====================
      //  Vue 3 Composition API setup 
      // 
      // 

      return {
        //  Store 
        //  Pinia store 
        //  dataStore.loadingdataStore.supervisorAgencies 
        dataStore,

        // 
        // 
        // 
        debugInfo,

        // 
        // 12
        //  v-for 12
        getSupervisorChartsData,
        exportContainerSvgAsPng,
      };
    }, // setup 
  }; // Vue 
</script>

<template>
  <div class="case-count-container">
    <div class="w-100 px-3">
      <div class="row">
        <div class="col-6">
          <div class="chart-container my-bgcolor-white rounded-4 border py-3 position-relative">
            <button
              class="btn btn-sm btn-outline-secondary position-absolute"
              style="
                top: 8px;
                right: 8px;
                z-index: 2;
                color: var(--my-color-gray-400);
                border-color: var(--my-color-gray-400);
              "
              title=" PNG"
              @click="exportContainerSvgAsPng('main-chart', '_.png')"
            >
              <i class="fa-solid fa-download"></i>
            </button>
            <div class="d-flex justify-content-center my-title-md-black">
               (12)
            </div>
            <div v-if="debugInfo.error" class="alert alert-danger mb-3">
              {{ debugInfo.error }}
            </div>
            <div v-if="debugInfo.loading" class="text-center text-muted mb-3">...</div>

            <div
              v-if="!debugInfo.hasData && !debugInfo.loading && !debugInfo.error"
              class="text-center text-muted mb-3"
            >
              
            </div>

            <div id="main-chart" style="min-height: 480px"></div>
          </div>
        </div>

        <div class="col-6">
          <div class="chart-container my-bgcolor-white rounded-4 border pt-3">
            <div class="d-flex justify-content-center my-title-md-black pb-3">
              
            </div>
            <div id="taiwan-map" style="height: 480px; border-radius: 0 0 1rem 1rem"></div>
          </div>
        </div>
      </div>

      <div class="row">
        <div v-for="chartData in getSupervisorChartsData" :key="chartData.id" class="col-3 mt-4">
          <div class="chart-container my-bgcolor-white rounded-4 border py-3 position-relative">
            <button
              class="btn btn-sm btn-outline-secondary position-absolute"
              style="
                top: 8px;
                right: 8px;
                z-index: 2;
                color: var(--my-color-gray-400);
                border-color: var(--my-color-gray-400);
              "
              title=" PNG"
              @click="exportContainerSvgAsPng(chartData.id, chartData.title + '_.png')"
            >
              <i class="fa-solid fa-download"></i>
            </button>
            <div class="d-flex justify-content-center my-title-md-black">
              {{ chartData.title }}
            </div>
            <div :id="chartData.id" style="min-height: 320px"></div>
          </div>
        </div>
      </div>

      <div class="row">
        <div class="col-12 mt-4">
          <div class="my-bgcolor-white rounded-4 border p-3 position-relative">
            <button
              class="btn btn-sm btn-outline-secondary position-absolute"
              style="
                top: 8px;
                right: 8px;
                z-index: 2;
                color: var(--my-color-gray-400);
                border-color: var(--my-color-gray-400);
              "
              title=" PNG"
              @click="exportContainerSvgAsPng('network-graph', '_.png')"
            >
              <i class="fa-solid fa-download"></i>
            </button>
            <div class="d-flex justify-content-center my-title-md-black mb-3">
              
            </div>

            <div id="network-graph" style="height: 600px; width: 100%"></div>
          </div>
        </div>
      </div>

      <!--  -->
      <div class="row">
        <div class="col-12 mt-4">
          <div class="my-bgcolor-white rounded-4 border p-3">
            <div class="d-flex justify-content-center my-title-md-black mb-3">
              
            </div>
            <div id="word-cloud" style="height: 400px; width: 100%"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

